.Promo{position:relative;overflow:hidden}.background{position:absolute;top:0;left:0;bottom:0;right:0;background:url(../../images/promo_background.jpg) no-repeat center/cover}.background_video{width:100%;height:100%;object-fit:cover;filter:brightness(0.7) contrast(1.1)}.background::after{content:"";position:absolute;inset:0;pointer-events:none;background:linear-gradient(135deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.6) 50%, rgba(0, 0, 0, 0.7) 100%)}.background::before{content:"";position:absolute;inset:0;pointer-events:none;background:radial-gradient(800px 600px at 20% 20%, rgba(248, 199, 35, 0.15), transparent 60%),radial-gradient(600px 400px at 80% 80%, rgba(0, 212, 255, 0.1), transparent 60%);z-index:1}.content{position:relative;min-height:100vh;z-index:2;padding:clamp(120px,8vw,160px) clamp(16px,3.6vw,48px) clamp(28px,3vw,48px);background:radial-gradient(1200px 800px at 10% 0%, rgba(248, 199, 35, 0.12), rgba(0, 0, 0, 0) 40%),radial-gradient(1000px 600px at 90% 100%, rgba(0, 212, 255, 0.08), rgba(0, 0, 0, 0) 40%),linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.8) 100%);display:grid;grid-template-columns:1.1fr .9fr;align-items:center;gap:clamp(32px,6vw,80px);max-width:1400px;margin:0 auto}@media(max-width: 1000px){.content{grid-template-columns:1fr;padding-top:140px;gap:clamp(24px,5vw,48px)}}@media(min-width: 1400px){.content{max-width:none;width:100%;padding-left:clamp(24px,4vw,64px);padding-right:clamp(24px,4vw,64px)}}.company_info{animation:slideUp 1s ease-out .3s both}.heading{font-family:"Unbounded","Orbitron",system-ui,sans-serif;font-weight:900;font-size:clamp(40px,5vw + 12px,96px);line-height:1.1;max-width:720px;background:var(--gradient-primary);-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0);text-shadow:0 0 40px rgba(248,199,35,.3);position:relative;margin-bottom:24px}.heading::after{content:"";position:absolute;bottom:-8px;left:0;width:120px;height:4px;background:var(--gradient-primary);border-radius:2px;box-shadow:0 0 20px rgba(248,199,35,.5)}.company_description{margin-top:32px;max-width:640px;font-size:clamp(18px,1.6vw + 10px,26px);color:var(--muted);font-weight:500;line-height:1.5;position:relative;padding-left:24px}.company_description::before{content:"→";position:absolute;left:0;top:0;color:var(--accent);font-size:1.2em;font-weight:700}.heading_main{display:block;background:var(--gradient-primary);-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0)}.heading_sub{display:block;background:var(--gradient-secondary);-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0);font-size:.7em;font-weight:700;margin-top:8px;letter-spacing:.1em}.features{display:flex;gap:24px;margin-top:32px;flex-wrap:wrap}.feature{display:flex;align-items:center;gap:8px;padding:12px 16px;background:hsla(0,0%,100%,.05);border:1px solid hsla(0,0%,100%,.1);border-radius:var(--radius-lg);font-size:14px;font-weight:600;color:var(--muted);transition:all var(--transition-normal)}.feature:hover{background:hsla(0,0%,100%,.08);border-color:var(--accent);color:var(--text);transform:translateY(-2px)}.feature_icon{font-size:18px;filter:grayscale(0.3);transition:filter var(--transition-normal)}.feature:hover .feature_icon{filter:grayscale(0)}.background_fallback{position:absolute;inset:0;background-size:cover;background-position:center;background-repeat:no-repeat}.calculator_wrapper{max-width:680px;min-width:320px;width:100%;backdrop-filter:blur(20px) saturate(180%);background:var(--gradient-surface);border:1px solid hsla(0,0%,100%,.15);border-radius:var(--radius-2xl);box-shadow:var(--shadow-1),0 0 60px rgba(248,199,35,.1),inset 0 1px 0 hsla(0,0%,100%,.1);padding:clamp(24px,3vw,32px);position:relative;animation:scaleIn 1s ease-out .6s both;transition:box-shadow var(--transition-normal),transform var(--transition-normal),border-color var(--transition-normal)}.calculator_wrapper:hover{box-shadow:var(--shadow-2),0 0 80px rgba(248,199,35,.12),inset 0 1px 0 hsla(0,0%,100%,.15);border-color:rgba(248,199,35,.28)}.calculator_wrapper:hover::before{opacity:.22}@media(max-width: 768px){.content{padding:120px 20px 40px;gap:32px}.heading{font-size:clamp(36px,8vw + 8px,64px);margin-bottom:20px}.heading::after{width:80px;height:3px}.company_description{margin-top:24px;font-size:clamp(16px,4vw + 8px,20px);padding-left:20px}.calculator_wrapper{padding:20px;border-radius:var(--radius-xl)}}@media(max-width: 480px){.content{padding:100px 16px 32px}.heading{font-size:clamp(32px,10vw + 6px,48px)}.company_description{padding-left:16px}}